.overlay {
  /*    */
  opacity: 0;
  visibility: hidden;

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 20;
  transition: transform var(--main-animation), visibility var(--main-animation),
    opacity var(--main-animation);
}

/*     */

.modal {
  /*    */
  opacity: 0;
  visibility: hidden;

  /*     z-index */
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -200%);
  transition: transform var(--main-animation), visibility var(--main-animation),
    opacity var(--main-animation);

  z-index: 30; /*      */
}

/*        */

.modal.active {
  opacity: 1;
  visibility: visible;
  transform: translate(-50%, -50%);
}

.overlay.active {
  opacity: 1;
  visibility: visible;
}
